import React from 'react';
import QrReader from 'react-qr-reader';
import { useHistory } from 'react-router-dom';

const HomePage = () => {

  const history = useHistory();
  const handleScan = (data) => {
    if(data) {
      history.push(`/result/${data}/0`);
    }

}

  const handleError = (err) => {
    console.error(err)
  }




  // const qrReaderClick = useRef(null);
  
  // const allowHandler = () => {
  //   qrReaderClick.current.openImageDialog();
  // }

  return (
    <div>
      <div className="Home__page">
        <QrReader
          // ref={qrReaderClick}
          delay={300}
          onError={handleError}
          onScan={handleScan}
          className="qrReader"
        />
      </div>
    </div>
  );
};

export default HomePage;
