import React, { useState, useEffect } from 'react';
import { sqlFromServer } from '../api';
import { useParams } from 'react-router-dom';
const Result = () => {

  const [sqlResult, setSqlResult] = useState([
      {
        nametv: 'Даного коду товару не знайдено',
        codetv: '1'
      }
    ]);
  const params = useParams();
  const inputval = params.inputval;
  const typeval = params.typeval;

  useEffect(() => {

    async function getData() {
      await sqlFromServer(inputval, typeval)
      .then(res => setSqlResult(res));
    };
    getData();

  },[inputval, typeval]);


  return (
    <div>
      <ul>
        {sqlResult.map(result => (
          <li
            key={result.codetv}
          >
            <span>{result.codetv}</span>
            <p>{result.nametv}</p>
            <p>{result.ved}</p>
            <p>{result.vimir}</p>
            <p>{result.codetv}</p>
          </li>
        ))
        }
        {console.log(sqlResult)}
      </ul>
    </div>
  );
}

export default Result;
